.message {
    border-radius: 40px; /* TODO: Change me*/
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    padding-left: 2em;
    padding-right: 2em;
    padding-top: 0em;
    display: -ms-flexbox;
    display: flex;
    /* max-width: 50%; */
}

.message p {
    font-size: .8rem;
    color: black;    
}

.resize {
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    max-width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding-top: 1em;
    padding-bottom: 1em;
}

.purple {
    background-color: #D0BBE6;
    -ms-flex-item-align: end;
        align-self: flex-end;
} 

.pink {
    background-color: #EFB1A7;
    -ms-flex-item-align: start;
        align-self: flex-start;
}
.time_label {
    /* TODO: Look into making this bigger */
    font-size: .75rem;
    color: #646464;
    padding-left: 3em;
    padding-bottom: .3em;
    
}

.right_hover {
    padding-right: 3em;
    padding-left: 0em;
    /* position: relative;
    float: right; */
    text-align: right;
}

.right_message {
    -ms-flex-item-align: end;
        align-self: flex-end;
}



